import React, { Component } from 'react';
//import { Link } from 'react-router-dom';
import SVG from 'react-inlinesvg';
import { baseUrl } from '../../../../../../Assets/AssetsHelper';

class NoAssetsUpload extends Component {

    render() {
        return <div className="block-center notFound_ py-4">
            <div className="d-flex flex-column align-items-center" style={{ maxWidth: 450 }}>
                <div className="mb-3 mb-xl-4">
                    <span className="svg-icon svg-icon-xxl me-0">
                        <SVG src={baseUrl('media/icons/svg-icons/assets-placeholder.svg')} height={32} width={32} />
                    </span>
                </div>
                <div className="mb-2 text-center">
                    <h5 className="text-gray-dark fw-semibold text-center">No Assets Available</h5>
                    <p>Looks like you have no assets available yet. Please start uploading content to get started.</p>
                </div>
                <button type="button" className="btn btn-primary mb-2">
                    <i className="ri-add-line"></i>
                    Upload Assets
                </button>
            </div>
        </div>
    }
}

export default NoAssetsUpload;