
import React, { Component } from 'react';
import { toast } from 'react-toastify';
import { connect } from "react-redux";
import CreatableSelect from 'react-select/creatable';
import PerfectScrollbar from 'react-perfect-scrollbar'
import NoAssetsUpload from './Elements/NoAssetsUpload';
import { OverlayTrigger, Tooltip } from 'react-bootstrap';
import Media from '../../../CommonComponents/Media/Media';
import { createAssets } from '../../../../../Services/Actions/assetsActions'
import TagDropdown from './Elements/tags'
import CaptionDropdown from './Elements/caption'

class UploadedAssets extends Component {
    state = {
        isRequest: false, selectedOption: null, listTags: [], tagString: [],
        newTag: ""
    }

    componentWillMount() {
        const { tagList } = this.props
        if (tagList && tagList.length > 0) {
            var listTags = []
            tagList.map((item) => {
                listTags.push({ value: item, label: item })
            })
            this.setState({ listTags })


        }
    }
    onClickUploaded = event => {
        const { collection, uploadedData } = this.props;
        if (uploadedData && uploadedData.length > 0) {
            this.setState({ isRequest: true })
            var assets = []
            uploadedData.map((item) => {
                assets.push({
                    content: item.content, postFile: item.postFile, copyrights: item.copyrights ? 1 : 0, mediaHeight: item.mediaHeight, mediaWidth: item.mediaWidth, tags: item.addTags, ext: item.ext, size: item.size, fileName: item.fileName
                })
            })

            var collectionId = []
            if (collection && collection.length > 0) {
                collection.map((iCollection) => {
                    collectionId.push(iCollection.value)

                })

            }
            const isCollection = collectionId && collectionId.length > 0 ? { "collection": collectionId.join(",") } : { "collection": "" }
            const data = {
                "assets": assets, ...isCollection
            }
            this.props.createAssets(data, this.onCallBack)
        }
        else {
            toast.error('Please select assets for upload.')
        }
    }
    onCallBack = () => {
        const { editMediaHandleCallback, history, uppyPostEdit, onClearUPloadObject } = this.props;
        this.setState({ isRequest: false })
        editMediaHandleCallback(false)
        uppyPostEdit.cancelAll()
        uppyPostEdit.removeFile()
        onClearUPloadObject([])
        history("/assets")
    }
    handleChange = (selectedOption) => {
        let { tagString } = this.state;
        if (!tagString.includes(selectedOption.value)) {
            tagString.push(selectedOption.value)
            this.props.onChangeTags(tagString.join(","))
            this.setState({ selectedOption: null, tagString })
        }
    };
    onClickToRemoveTag = (index) => event => {
        let { tagString } = this.state;
        tagString.splice(index, 1)
        this.setState({ tagString }, () => this.props.onChangeTags(tagString.join(",")))

    }
    render() {

        const { uploadedData, selected, onClickTORemovePost, onClickTOCheckPost, updateStatus, updateStatusC, updateStatusTag, updateStatusCopy, captionStatus, tagsStatus, copyrightStatus, onChangeCaption, onChangeTags, onChangeCopyRight, captionText, tagstext, copyrightValue, onClickToAllCheck, checkAll, onCaptionSingle, onAddTagSingle, onCopyRightSingle, onAddTagSubmit, onSubmitCaption, onResetCaption, onAddTagReset } = this.props
        const { isRequest, listTags, tagString } = this.state
        return <div className="d-flex align-items-start flex-column flex-lg-row w-100">
            <div className="innerModalAside flex-shrink-0 p-3">
                <div className="card">
                    <div className="card-body">
                        <PerfectScrollbar>
                            <div className="innerModalAsideWrapper p-xxl-1">
                                <div className="d-flex align-items-center justify-content-between border-bottom pb-3 mb-4 d-lg-none">
                                    <h1 className="text-dark font-weight-bold mr-4 fs-6 mb-0">Edit Image</h1>
                                    <div className="d-flex align-items-start">
                                        <button type="button" className="btn-close text-light" onClick={this.modalToggle}></button>
                                    </div>
                                </div>
                                <label className="form-check flex-grow-1 justify-content-between border-bottom pb-3 mb-3">
                                    <span className="form-check-label ps-0 me-1">Select All ({selected})</span>
                                    <input className="form-check-input" type="checkbox" name="" checked={checkAll} onClick={onClickToAllCheck} />
                                </label>
                                <div className="border-bottom pb-3 mb-3">
                                    <div className="form-check form-switch justify-content-between mb-2">
                                        <div className="d-flex flex-grow-1">
                                            <label className="form-check-label ps-0" htmlFor="mediaCaption">Caption</label>
                                            <OverlayTrigger placement="top" overlay={<Tooltip>Add a caption to all selected assets.</Tooltip>}>
                                                <i className="ri-information-fill icon-6 text-muted ms-1 fw-normal"></i>
                                            </OverlayTrigger>
                                        </div>
                                        <input className="form-check-input" type="checkbox" id="mediaCaption" value={captionStatus} onClick={updateStatusC} />
                                    </div>
                                    <textarea type="text" className="form-control textarea-min-row-4" placeholder="Add your caption here..." rows={6} onChange={onChangeCaption} value={captionText}></textarea>
                                </div>
                                <div className="border-bottom pb-3 mb-3">
                                    <div className="form-check form-switch justify-content-between mb-2">
                                        <div className="d-flex flex-grow-1">
                                            <label className="form-check-label ps-0" htmlFor="addTag">Add Tag</label>
                                            <OverlayTrigger placement="top" overlay={<Tooltip>Select assets and add a tag to categorize your visuals.</Tooltip>}>
                                                <i className="ri-information-fill icon-6 text-muted ms-1 fw-normal"></i>
                                            </OverlayTrigger>
                                        </div>
                                        <input className="form-check-input" type="checkbox" value={tagsStatus} id="addTag" onClick={updateStatusTag} />
                                    </div>
                                    <div className="mb-2">
                                        <CreatableSelect className="selectpicker" classNamePrefix="selectpicker" isClearable options={listTags} onChange={this.handleChange} value={this.state.selectedOption} />
                                    </div>
                                    {tagString && tagString.length > 0 ? <div className="d-flex w-100 flex-wrap w-100">
                                        {
                                            tagString.map((tagItem, tagIndex) => <div className="badge bg-secondary rounded-pill text-gray-800 border me-1 mb-1 px-2 d-flex align-items-center" style={{ top: 0, maxWidth: '100%' }}>
                                                <span className="text-break" style={{ whiteSpace: 'normal' }}>{tagItem}</span>
                                                <button className="btn p-0 ms-1" onClick={this.onClickToRemoveTag(tagIndex)}>
                                                    <i className="ri-close-line me-0"></i>
                                                </button>

                                            </div>)
                                        }
                                    </div> : null}

                                </div>
                                <div className="pb-3 mb-3">
                                    <div className="form-check form-switch justify-content-between mb-2">
                                        <div className="d-flex flex-grow-1">
                                            <label className="form-check-label ps-0" htmlFor="mediaCopyright">Copyright</label>
                                            <OverlayTrigger placement="top" overlay={<Tooltip>Get copyright for your content and avoid any legal issues in the future.</Tooltip>}>
                                                <i className="ri-information-fill icon-6 text-muted ms-1 fw-normal"></i>
                                            </OverlayTrigger>
                                        </div>
                                        <input className="form-check-input" type="checkbox" value={copyrightStatus} id="mediaCopyright" onClick={updateStatusCopy} />
                                    </div>
                                    <div className="d-flex align-items-center pt-1">
                                        <div className="form-check me-3">
                                            <input className="form-check-input" type="radio" name="copyright" value={copyrightValue == true ? `yes` : ``} id="copyrightYes" onClick={onChangeCopyRight(true)} />
                                            <label className="form-check-label text-muted" htmlFor="copyrightYes">Yes</label>
                                        </div>
                                        <div className="form-check">
                                            <input className="form-check-input" type="radio" name="copyright" value={copyrightValue == false ? `no` : ``} id="copyrightNo" onClick={onChangeCopyRight(false)} />
                                            <label className="form-check-label text-muted" htmlFor="copyrightNo">No</label>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </PerfectScrollbar>
                        <div className="innerAsideActions p-xxl-1">
                            <button className={`btn btn-primary w-100 ${isRequest ? `spinner disabled` : ``}`} onClick={this.onClickUploaded}>Upload Assets ({uploadedData.length})</button>
                        </div>
                    </div>
                </div>
            </div>
            <div className="innerModalWrap d-flex flex-grow-1 w-100 flex-column py-3">
                <div className="container-fluid">
                    {uploadedData ? <div className="row gx-2">
                        {uploadedData.map((item, uIndex) => {
                            return <div className="uploadedAssetsGrid" key={item.postId}>
                                <div className="card card-custom mb-2">
                                    <div className="card-body d-flex flex-column p-2">
                                        <div className="d-flex align-items-center justify-content-between pb-2">
                                            <div className="d-flex align-items-start">
                                                <label className="form-check me-2">
                                                    <input className="form-check-input" type="checkbox" name="" value="phone" checked={item.checked} onClick={onClickTOCheckPost(uIndex)} />
                                                </label>
                                            </div>
                                            <div className="d-flex align-items-start">
                                                <button type="button" className="btn btn-icon h-20px w-20px" onClick={onClickTORemovePost(uIndex, item.localId)}>
                                                    <i className="ri-close-line icon-4 me-0"></i>
                                                </button>
                                            </div>
                                        </div>
                                        <div className="d-flex align-items-center flex-column cursor-pointer">
                                            <Media
                                                mediaType={2}
                                                postFile={item.postFile}
                                                socialNetwork={0}
                                                textContent={item.content}
                                                rating={4}
                                                mediaHeight={466}
                                                mediaWidth={878} />
                                            <div className="w-100 pt-2">

                                                <TagDropdown uIndex={uIndex} item={item} onAddTagReset={onAddTagReset} onAddTagSubmit={onAddTagSubmit} onAddTagSingle={onAddTagSingle} {...item} />
                                                <CaptionDropdown uIndex={uIndex} onResetCaption={onResetCaption} item={item} onCaptionSingle={onCaptionSingle} onSubmitCaption={onSubmitCaption} />
                                                <div className="d-flex flex-wrap justify-content-between pt-2">
                                                    <label className="text-gray-800 fs-9 pe-2">Copyright</label>
                                                    <div className="d-flex align-items-center">
                                                        <div className="form-check me-3">
                                                            <input className="form-check-input" type="radio" id="copyrightPostYes" checked={item.copyright == true ? true : false} onClick={onCopyRightSingle(uIndex, true)} />
                                                            <label className="form-check-label fs-9" htmlFor="copyrightPostYes">Yes</label>
                                                        </div>
                                                        <div className="form-check">
                                                            <input className="form-check-input" type="radio" id="copyrightPostNo" checked={item.copyright == false ? true : false} onClick={onCopyRightSingle(uIndex, false)} />
                                                            <label className="form-check-label fs-9" htmlFor="copyrightPostNo">No</label>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                        })
                        }

                    </div> : <NoAssetsUpload />}
                </div>
            </div>
        </div>
    }
}

const mapDispatchToProps = dispatch => {
    return {
        createAssets: (data, callback) => dispatch(createAssets(data, callback)),
    }
}

const mapStateToProps = (state) => {
    return {
        tagList: state.tags.tagList
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(UploadedAssets);