import React, { Component } from 'react';
import { connect } from "react-redux";
import PerfectScrollbar from 'react-perfect-scrollbar'
import { OverlayTrigger, Tooltip, Dropdown } from 'react-bootstrap';
import { toast } from "react-toastify";

class TagDropdown extends Component {
    constructor(props) {
        super(props)
        this.state = {
            tagString: props.item.addTags ? String(props.item.addTags).split(",") : [],
            newTag: "",
            listTags: [],
            selectedOption: null,
            isShow: false,
            isOpen: false
        }
    }
    onClickToRemoveTag = (index) => event => {
        let { tagString } = this.state;
        const { uIndex, onAddTagSubmit } = this.props
        tagString.splice(index, 1)
        onAddTagSubmit(uIndex, tagString.join(","))
        this.setState({ tagString })

    }

    componentWillMount() {
        const { tagList } = this.props
        if (tagList && tagList.length > 0) {
            var listTags = []
            tagList.map((item) => {
                listTags.push({ value: item, label: item })
            })
            this.setState({ listTags })


        }

    }

    componentDidMount() {
        const { item } = this.props
        this.setState({ tagString: String(item.addTags).split(",") })
    }

    onClickNewAddTag = (newTag, uIndex) => event => {
        const { tagString } = this.state;
        if (newTag && String(newTag).length >= 3) {
            const filterdData = tagString.filter((item) => item == newTag)
            if (filterdData == 0) tagString.push(newTag)
            const texttagsearch = document.querySelector(`#texttagsearch${uIndex}`)
            if (texttagsearch) {
                texttagsearch.value = ""
            }
            //this.props.onAddTagSubmit(uIndex,tagString.join(","))
            this.setState({ tagString, selectedOption: null, isShow: false })
        }
        else {

        }
    }
    onClickNewAddTagEnter = event => {
        const { tagList, uIndex } = this.props
        if (event.key === 'Enter' || event.keyCode === 13) {
            const { tagString } = this.state;
            if (event.target.value && String(event.target.value).length >= 3) {
                const filterdData = tagString.filter((item) => item == event.target.value)
                if (filterdData == 0) tagString.push(event.target.value)
                const texttagsearch = document.querySelector(`#texttagsearch${uIndex}`)
                if (texttagsearch) {
                    texttagsearch.value = ""
                }
                //this.props.onAddTagSubmit(uIndex,tagString.join(","))
                if (tagList && tagList.length > 0) {
                    var listTags = []
                    tagList.map((item) => {
                        listTags.push({ value: item, label: item })
                    })
                    this.setState({ listTags })
                }
                this.setState({ tagString, selectedOption: null, isShow: false })
            }
            else {
                var toastID = toast.loading("Loading...");
                toast.update(toastID, { render: "Tag length should be greater than 2", type: "error", isLoading: false, autoClose: 3000 })
            }
        }
    }
    onClickSubmit = (uIndex) => event => {
        var { tagString } = this.state;
        tagString = tagString.filter((fitem) => String(fitem).length > 0)
        this.props.onAddTagSubmit(uIndex, tagString.join(","))
        this.setState({ isOpen: false })
    }
    addNewTag = event => this.setState({ newTag: event.target.value })



    setNewTags = (selectedOption) => {

        if (selectedOption && selectedOption.value) this.setState({ newTag: selectedOption.value, selectedOption })
        else this.setState({ newTag: "", selectedOption: null })


    };
    onSearchtag = event => {
        const { tagList } = this.props

        if (event.target.value && String(event.target.value).length > 0) {
            const filterData = tagList.filter((item) => String(item).toLowerCase().startsWith(event.target.value))
            if (filterData && filterData.length > 0) {
                var listTags = []
                filterData.map((item) => {
                    listTags.push({ value: item, label: item })
                })
                this.setState({ listTags })
            }
            else {
                this.setState({ listTags: [] })
            }
        }
        else {
            if (tagList && tagList.length > 0) {
                var listTags = []
                tagList.map((item) => {
                    listTags.push({ value: item, label: item })
                })
                this.setState({ listTags })
            }
        }
    }
    onClickToResetTag = (uIndex) => event => {
        this.setState({ tagString: [] }, () => this.props.onAddTagReset(uIndex, ""))
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.item.addTags && this.state.tagString != String(nextProps.item.addTags).split(",")) this.setState({ tagString: String(nextProps.item.addTags).split(",") })
    }
    onClickToggle = event => {
        const { isOpen } = this.state
        this.setState({ isOpen: !isOpen })
    }
    render() {

        const { tagString, newTag, listTags, selectedOption, isOpen } = this.state
        const { uIndex, item, onAddTagReset, onAddTagSubmit, onAddTagSingle } = this.props

        return <Dropdown className="position-relative" id={`tag${uIndex}`} show={isOpen} onToggle={this.onClickToggle}>
            <Dropdown.Toggle variant='btn' className="d-flex input-group p-0 arrow_disabled mb-2">
                <input name="enterTags" className="form-control form-control-sm fs-10 d-flex overflow-hidden" style={{ minHeight: 30 }} placeholder="Enter Tags..." aria-label="Enter Tags..." aria-describedby="enterTags" readOnly={true} value={item.addTags} />

                <button type="button" className="input-group-text svg-icon svg-icon-7 me-0 px-2 text-gray-800" id="enterTags" onClick={this.onClickToggle}>
                    <i className="ri-add-line me-0"></i>
                </button>
            </Dropdown.Toggle>
            <Dropdown.Menu className="popover p-2 bg-light position-absolute start-0 top-0 w-100 rounded-1 box-shadow">
                <div className="d-flex align-items-center justify-content-between mb-2">
                    <div className="fs-9 fw-semibold text-gray-800">Add Tags</div>
                    <OverlayTrigger placement="top" overlay={<Tooltip>Reset</Tooltip>}>
                        <button type="button" className="btn btn-sm btn-icon btn-active-light btn-active-color-primary p-0 h-24px w-24px" onClick={this.onClickToResetTag(uIndex)}>
                            <i className="ri-arrow-go-back-line icon-7"></i>
                        </button>
                    </OverlayTrigger>
                </div>
                <div className="d-flex w-100 flex-wrap">
                    {
                        tagString.map((itemtag, indexItemtag) => {
                            return itemtag && String(itemtag).length > 0 ? <div className="badge bg-secondary rounded-pill text-gray-800 border me-1 mb-1 px-2 d-flex align-items-center" style={{ top: 0, maxWidth: '100%' }}>
                                <span className="text-break" style={{ whiteSpace: 'normal' }}>{itemtag}</span>
                                <button className="btn p-0 ms-1" onClick={this.onClickToRemoveTag(indexItemtag)}>
                                    <i className="ri-close-line me-0"></i>
                                </button>
                            </div> : null
                        })
                    }
                </div>
                <div className="dropdown">
                    <input id={`texttagsearch${uIndex}`} className="form-control form-control-sm fs-10 d-flex overflow-hidden" style={{ minHeight: 30 }} placeholder="Enter Tags..." autoComplete="off" onChange={this.onSearchtag} onClick={event => this.setState({ isShow: true })} onKeyUp={this.onClickNewAddTagEnter} />
                    <div className={`dropdown-menu p-2 w-100 ${this.state.isShow ? `show` : ``}`}>
                        <PerfectScrollbar>
                            <div className="list-autocomplete w-100" style={{ maxHeight: 150 }}>
                                {
                                    listTags.map((tagItem, tagIndex) => <button onClick={this.onClickNewAddTag(tagItem.label, uIndex)} key={tagIndex} type="button" className="dropdown-item fs-9 py-1 px-2 text-ellipsis d-block">{tagItem.label}</button>)
                                }
                            </div>
                        </PerfectScrollbar>
                    </div>
                </div>
                <div className="w-100 d-flex justify-content-end pt-2">
                    <button type="submit" className="btn btn-xs btn-primary" onClick={this.onClickSubmit(uIndex)}>Done</button>
                </div>
            </Dropdown.Menu>
        </Dropdown>
    }
}
const mapStateToProps = (state) => {
    return {
        tagList: state.tags.tagList
    }
}
export default connect(mapStateToProps)(TagDropdown);